:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;
  
}
body{
  background-color: #222222;
  -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#Header{
  font-size: var(--Nav-font-size);
  box-sizing: border-box;
  margin:0 auto;
  width: 94.5vw;
  min-height: 55px;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: 10px;}
#NavHeader-ul a{
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
}
#divImg{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}

#Search{
  background-color: #1a1a1a;
  border-radius: 5px;
  position: relative;
  display: block;
  padding: 5px 12px;
  text-decoration: none;
  padding-left: 36px;
}
#Search::after{
  position: absolute;
  top: 4px;
  left: 6px;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  min-height: 35px;
  min-width: 35px;
  width: 2.65vw;
  height: 2.65vw;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }





#Error {
  width: 100%;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 1vw 0;
  width: 100%;
  user-select: none;
}

#MainSliderContainer{
  min-height: 175px;
  height: 24.76vw;
  transition: .5s;
  position: relative;
  display: flex;
}
.MainSliderChild{
  background-color: #1f1f1f;
  min-width: 100%;
}
.MainSliderChild a{
  outline: none;
  text-decoration: none;
  height: 100%;
  cursor: default;
  display: flex;
}

.MainSliderChild img{
  background-color: #141414;
  min-width: 130px;
  width: 21vw;
  height: 100%;
}
.MainSliderChild div{
  overflow-y: hidden;
  pointer-events: none;
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
}
.MainSliderChild div h2, .MainSliderChild div p{
  margin: 0px;
  padding: 0px;
}
.dot{
  display: block;
  min-width: 15px;
  min-height: 15px;
  width: 1.15vw;
  height: 1.15vw;
  background: transparent;
  border-radius: 100%;
  border: 1px solid white;
  margin-right: 1%;
}
#Dots{
  display: flex;
  min-height: 40px;
  height: 4vw;
  box-sizing: border-box;
  min-width: 130px;
  width: 10vw;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}

.dot:focus{
  background: #a54200;
  outline: none;
}
.dot.focus{
  background: #ff6600;
}


#HotTitles{
  background-color: #1d1d1d;
  display: flex;
  width: 100%;
  height: 14.85vw;
  min-height: 200px;
}

#HotTitlesTop{
  position: relative;
  overflow: hidden;
  width: 40%;
  height: 100%;
}
#HotTitlesSlider{
  background-color: #1d1d1d;
  overflow: hidden;
  width: 60%;
  height: 100%;
}
#HotTitlesSliderContainer{
  height: 100%;
  display: flex;
  align-items: center;
  width:100%;
}
#HotTitlesTopContainer{
  transition: .3s;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#HotTitlesTopContainer a{
  display: block;
}
.HotTitlesTopChild img{
  transition: .3s;
  display: block;
  min-width: 110px;
  min-height: 160px;
  width: 8.304vw;
  height: 12.1vw;
  background-color: #141414;
}
.HotTitlesTopChildFocus a:focus, .HotTitlesTopChild a:focus{
  outline: 2px solid rgb(223, 52, 0);
}
.HotTitlesTopChildFocus img{
  transition: .3s;
  display: block;
  min-width: 139px;
  min-height: 190px;
  width: 10.4917vw;
  height: 14.35vw;
  background-color: #141414;
}
.HotTitlesTopDescription{
  text-decoration: none;
  box-sizing: border-box;
  background-color: #141414;
  transition: .5s;
  height: 100%;
  padding: 25px;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
.HotTitlesSliderChild{
  flex: 0 0 auto;
  width: 11vw;
  min-width: 130px;
}

.HotTitlesSliderChild img{
  width: 8.304vw;
  height: 12.1vw;
  min-width: 110px;
  min-height: 160px;
  background-color: #141414;
}












@media screen and (max-width:500px) {
  #Dots {
    margin-top: 5px;
  }
}
@media screen and (max-width:700px) {
  #MainSlider {
    font-size: .8em;
  }
  #HotTitles {
    display: block;
  }
  #HotTitlesTop{
    min-width: 100%;
  }
  #HotTitlesSlider{
    min-width: 100%;
  }
}
@media screen and (max-width: 768px) {
  #MainSlider {
    font-size: .85em;
  }
  .HotTitlesTopDescription {
    font-size: .85em;
  }
}
@media screen and (max-width: 1400px) {
  .HotTitlesTopDescription {
    font-size: 1.05em;
  }
}
@media screen and (max-width: 1200px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
}
@media screen and (max-width: 992px) {
  .HotTitlesTopDescription {
    font-size: .98em;
  }
}

@media screen and (max-width: 576px) {
  .HotTitlesTopDescription{
    font-size: .8em;
  }
}


.pointEvent{
  pointer-events: none;
}





